
body {
  background-color: #0f0f0f;
  color: #fff;
  font-family: Verdana, sans-serif;
  font-style: normal;
  line-height: 1.5;
  margin: 0 auto;
  width: 90%;
}

figure {
  margin: 0;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

input {
  border: 0;
  font: inherit;
  margin: 0;
  padding: 0;
}

.search {
  font-size: 1.5rem;
  margin: 1.5em auto 3em;
  max-width: 80%;
}

.search [type='search'] {
  background-color: #ffffff;
  color: #898F9C;
  padding: 0.5em 2em;
  text-align: center;
  width: 100%;
}

.movies {
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fill, minmax(10em, 1fr));
  grid-template-rows: 290px 290px 290px 290px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.movies > li {
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.3);
}

.movie figcaption {
  margin-top:20px;
  height:100px;
  position:relative;
  bottom:150px;
  padding: 1em;
  display:flex;
  justify-content:center;
  align-items:center;
  background-color:black;
  opacity:0;
}
.movie:hover figcaption{
  opacity:0.8;
  cursor:pointer;
}

.movie__title {
  font-size: 1rem;
  font-weight: normal;
  font-variant: small-caps;
  margin: 0;
}

.footer {
  background-color: black;
  color: gray;
  line-height: 20px;
  padding-top: 20px;
  padding-bottom: 6px;
  width: 100%;
  bottom: 0;
  text-align: center;
  font-family: Sans-serif;
}  
@media and (max-width:375px){
  .search{
    font-size: 8px;
  }
}
